import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Field, Form, Formik } from 'formik';
import * as Yup from 'yup';

import axios from 'axios-instance';
import openNotification from 'utils/openNotification';

import Workspace from 'components/Workspace';
import Header from 'components/Header';
import { TableHeader, TableRow } from 'components/Table';
import Status from 'components/Status';
import Spinner from 'components/Spinner';
import Button from 'components/Button';
import SearchBar from 'components/Searchbar';
import Label from 'components/Label';
import Input from 'components/Input';
import ErrorMessage, { ErrorMessageStyled } from 'components/ErrorMessage';
import Select from 'components/Select';

const HeadingBar = styled.div`
  display: flex;
  justify-content: space-between;
`;
const Separate = styled.div`
  display: flex;
  align-items: flex-end;
  justify-content: ${({ mode }) => mode === 'edit' ? 'space-between' : 'flex-end'};
  margin-bottom: 28px;
`;
const ButtonStyled = styled(Button)`
  padding: 0;
  width: 100px;
`;

const validationSchema = Yup.object({
  name: Yup.string()
    .required('Pole wymagane.'),
  portion: Yup.number()
    .min(0)
    .required('Pole wymagane.'),
  amount: Yup.number()
    .min(0)
    .required('Pole wymagane.'),
  unit: Yup.string()
    .required('Pole wymagane.'),
  price: Yup.number()
    .positive('Wartość musi być dodatnia.')
    .required('Pole wymagane.'),
  priceMargin: Yup.number()
    .positive('Wartość musi być dodatnia.')
    .required('Pole wymagane.'),
});

const Bakery = props => {
  const {
    setFieldValue,
    error,
    setError,
    editLoading,
    setEditLoading,
    mode,
    setMode,
    defaultBakery,
    bakeryId,
    setBakeryId,
    bakery,
    bakeryLoading,
    fetchBakery
  } = props;

  const [searchValue, setSearchValue] = useState('');

  const fields = ['name', 'portion', 'amount', 'unit', 'price', 'priceMargin'];

  const goToEditMode = ingredient => {
    setMode('edit');
    fields.forEach(field => setFieldValue(field, ingredient[field]));
    setBakeryId(ingredient.id);
  };

  const goToAddMode = () => {
    setMode('add');
    fields.forEach(field => setFieldValue(field, defaultBakery[field]));
    setBakeryId(null);
  };

  const tryDeleteIngredient = async () => {
    setEditLoading(true);

    try {
      const response = await axios({
        method: 'delete',
        url: `bakery/${bakeryId}`,
      });
      if (response.status === 200) {
        goToAddMode();
        setEditLoading(false);
        await fetchBakery();
        openNotification('success', 'Usunięto wyrób cukierniczy', 3000);
      }
    } catch (err) {
      setError(err.response.data);
      setEditLoading(false);
    }
  };

  const layout = '2fr 1fr 1fr 1fr 1fr 85px';

  return (
    <Workspace>
      <div>
        <HeadingBar>
          <Header huge>Wyroby cukiernicze</Header>
          {mode === 'edit' && <Button onClick={goToAddMode} primary>+ Dodaj</Button>}
          <SearchBar value={searchValue} setValue={setSearchValue}/>
        </HeadingBar>
        <div>
          <TableHeader layout={layout}>
            {['nazwa', 'porcja', 'ilość', 'cena', 'marża', 'dostępność'].map(el => <div key={el}>{el}</div>)}
          </TableHeader>
          {bakeryLoading && <Spinner/>}
          {bakery
            .filter(({ name }) => !searchValue || name.includes(searchValue))
            .map(bakeryItem => {
              const unit = bakeryItem.unit === 'kilogram' ? 'kg' : bakeryItem.unit;
              return (
                <TableRow
                  key={bakeryItem.id}
                  layout={layout}
                  onClick={() => goToEditMode(bakeryItem)}
                  active={bakeryId === bakeryItem.id}
                >
                  <div>{bakeryItem.name}</div>
                  <div>{`${bakeryItem.portion} ${unit}`}</div>
                  <div>{`${bakeryItem.amount} ${unit}`}</div>
                  <div>{`${bakeryItem.price.toFixed(2)} zł/${unit}`}</div>
                  <div>{`${bakeryItem.priceMargin.toFixed(2)} zł/${unit}`}</div>
                  <Status status={bakeryItem.status}/>
                </TableRow>
              );
            })}
        </div>
      </div>
      <div>
        <Header>{mode === 'add' ? 'Dodaj' : 'Edytuj'}</Header>
        {editLoading ? (
          <Spinner/>
        ) : (
          <Form>
            <Label htmlFor="name">Nazwa</Label>
            <Field name="name">
              {({ field }) => <Input type="text" {...field} />}
            </Field>
            <ErrorMessage name="name"/>
            <Label htmlFor="portion">Porcja</Label>
            <Field name="portion">
              {({ field }) => <Input type="number" {...field} />}
            </Field>
            <ErrorMessage name="portion"/>
            <Label htmlFor="amount">Ilość</Label>
            <Field name="amount">
              {({ field }) => <Input type="number" {...field} />}
            </Field>
            <ErrorMessage name="amount"/>
            <Label htmlFor="unit">Jednostka</Label>
            <Field
              name="unit"
              component={Select}
              options={[
                { value: 'kilogram', label: 'kilogram' },
                { value: 'sztuka', label: 'sztuka' }
              ]}
            />
            <ErrorMessage name="unit"/>
            <Label htmlFor="price">Cena</Label>
            <Field name="price">
              {({ field }) => <Input type="number" {...field} />}
            </Field>
            <ErrorMessage name="price"/>
            <Label htmlFor="priceMargin">Marża</Label>
            <Field name="priceMargin">
              {({ field }) => <Input type="number" {...field} />}
            </Field>
            <ErrorMessage name="priceMargin"/>
            <Separate mode={mode}>
              {mode === 'edit' && (
                <ButtonStyled onClick={tryDeleteIngredient}>
                  Usuń
                </ButtonStyled>
              )}
              <ButtonStyled type="submit" primary>
                Zatwierdź
              </ButtonStyled>
            </Separate>
          </Form>
        )}
        {error && <ErrorMessageStyled>{error}</ErrorMessageStyled>}
      </div>
    </Workspace>
  )
};

const BakeryContainer = () => {
  const [editLoading, setEditLoading] = useState(false);
  const [error, setError] = useState(null);
  const [mode, setMode] = useState('add');
  const [bakery, setBakery] = useState([]);
  const [bakeryLoading, setBakeryLoading] = useState(true);

  const [bakeryId, setBakeryId] = useState(null);

  const defaultBakery = { name: '', portion: 1, amount: 1, unit: 'kilogram', price: 1, priceMargin: 0 };

  const fetchBakery = async () => {
    const response = await axios.get('bakery');

    if (response.data) {
      setBakery(response.data);
      setBakeryLoading(false);
    }
  };
  useEffect(() => {
    fetchBakery();
  }, []);

  const tryAddBakery = async (values) => {
    const { name, portion, amount, unit, price, priceMargin } = values;
    setEditLoading(true);

    try {
      const response = await axios({
        method: 'post',
        url: 'bakery',
        data: {
          name, portion, amount, unit, price, priceMargin
        }
      });
      if (response.status === 200) {
        setEditLoading(false);
        await fetchBakery();
        openNotification('success', 'Dodano wyrób cukierniczy', 3000);
      }
    } catch (err) {
      setError(err.response.data);
      setEditLoading(false);
    }
  };

  const tryEditBakery = async (values) => {
    const { name, portion, amount, unit, price, priceMargin } = values;
    setEditLoading(true);

    try {
      const response = await axios({
        method: 'put',
        url: `bakery/${bakeryId}`,
        data: {
          name, portion, amount, unit, price, priceMargin
        }
      });
      if (response.status === 200) {
        setEditLoading(false);
        await fetchBakery();
        openNotification('success', 'Wprowadzono zmiany.', 3000);
      }
    } catch (err) {
      setError(err.response.data);
      setEditLoading(false);
    }
  };

  return (
    <Formik
      initialValues={defaultBakery}
      validationSchema={validationSchema}
      onSubmit={values => {
        if (mode === 'add')
          tryAddBakery(values);
        else
          tryEditBakery(values);
      }}
    >
      {({ setFieldValue }) => (
        <Bakery
          setFieldValue={setFieldValue}
          error={error}
          setError={setError}
          editLoading={editLoading}
          setEditLoading={setEditLoading}
          mode={mode}
          setMode={setMode}
          defaultBakery={defaultBakery}
          bakeryId={bakeryId}
          setBakeryId={setBakeryId}
          bakery={bakery}
          bakeryLoading={bakeryLoading}
          fetchBakery={fetchBakery}
        />
      )}
    </Formik>
  )
};

export default BakeryContainer;